/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
	color: black;
	font-family: "Roboto", sans-serif;
	font-size: 1rem;
	line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
}

p {
	margin-bottom: 1.5em;
	padding: 1rem;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/* Elements
--------------------------------------------- */
body {
	background: #fff;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto;
	max-width: 100%;
}

figure {
	margin: 1em 0;
}

table {
	width: 100%;
}

/* Links
--------------------------------------------- */
a {
	color: #14213d;
}
a:visited {
	color: #14213d;
}
a:hover, a:focus, a:active {
	color: #14213d;
}
a:focus {
	outline: thin dotted;
}
a:hover, a:active {
	outline: 0;
}

/* Forms
--------------------------------------------- */
button,
input[type=button],
input[type=reset],
input[type=submit] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	line-height: 1;
	padding: 0.6em 1em 0.4em;
}
button:hover,
input[type=button]:hover,
input[type=reset]:hover,
input[type=submit]:hover {
	border-color: #ccc #bbb #aaa;
}
button:active, button:focus,
input[type=button]:active,
input[type=button]:focus,
input[type=reset]:active,
input[type=reset]:focus,
input[type=submit]:active,
input[type=submit]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
input[type=number],
input[type=tel],
input[type=range],
input[type=date],
input[type=month],
input[type=week],
input[type=time],
input[type=datetime],
input[type=datetime-local],
input[type=color],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
input[type=number]:focus,
input[type=tel]:focus,
input[type=range]:focus,
input[type=date]:focus,
input[type=month]:focus,
input[type=week]:focus,
input[type=time]:focus,
input[type=datetime]:focus,
input[type=datetime-local]:focus,
input[type=color]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Layouts
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
/* Navigation
--------------------------------------------- */
.main-navigation {
	bottom: 0;
	right: 0;
	position: fixed;
	display: block;
	width: 100%;
	z-index: 10000;
	width: 100%;
}
.main-navigation .hamburger {
	display: flex;
	justify-content: center;
	flex-direction: column;
	z-index: 1000;
}
.main-navigation .bar {
	width: 20px;
	height: 1px;
	background: white;
	margin: 0 auto 6px;
}
.main-navigation .bar:nth-of-type(2) {
	width: 25px;
}
.main-navigation button.menu-toggle {
	background: black;
	width: 3.5rem;
	height: 3.5rem;
	border-radius: 50%;
	margin: 10px;
	z-index: 10;
}
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
	position: absolute;
	bottom: 0;
	transform: translate(-50%);
	width: 100%;
	right: -50%;
	z-index: -1;
}
.main-navigation li {
	position: relative;
	background: rgba(255, 255, 255, 0.95);
	text-align: center;
}
.main-navigation li:hover {
	color: white;
}
.main-navigation a {
	display: block;
	text-decoration: none;
	color: black;
	padding: 0.5rem;
}
.main-navigation a:hover {
	color: black;
}
.main-navigation a:hover path {
	fill: white;
}
.main-navigation button {
	float: right;
}

.menu-toggle,
.main-navigation.toggled ul {
	display: block;
	background: transparent;
	border: none;
}

.main-navigation ul.sub-menu {
	display: none;
}

/* Highlight using link element */
li.current-menu-item > a {
	text-decoration: underline;
}

@media screen and (min-width: 37.5em) {
	.main-navigation {
		bottom: initial;
		right: initial;
		position: relative;
	}
	.main-navigation li {
		background: initial;
	}

	.menu-toggle {
		display: none;
	}

	.main-navigation ul {
		display: flex;
		justify-content: flex-end;
		position: relative;
		padding-top: 1.25rem;
		z-index: 1;
	}
	.main-navigation ul li a {
		color: white;
		fill: white;
	}
	.main-navigation ul li a:hover {
		color: #fca311;
	}
	.main-navigation ul li a:hover path {
		fill: white;
	}
	.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
		left: auto;
	}
}
.site-main .comment-navigation,
.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
}
.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
}
.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}
.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	flex: 1 0 50%;
}

.main-navigation {
	font-size: 1rem;
}

@media (min-width: 1000px) {
	.main-navigation {
		font-size: 1.25rem;
	}
}
/* Footer
--------------------------------------------- */
.site-footer {
	padding-top: 30px;
	display: grid;
	background-color: white;
	margin: 0 auto;
	grid-template-columns: 1fr 1fr;
	color: black;
	grid-template-rows: auto;
	gap: 10px 0px;
	grid-auto-flow: row;
	grid-template-areas: "contactInfo siteMap" "googleMap googleMap" "copyright copyright";
}
.site-footer .infoWrapperFooter {
	grid-area: contactInfo;
	align-self: flex-start;
	padding-left: 1.5rem;
}
.site-footer .infoWrapperFooter p,
.site-footer .infoWrapperFooter a {
	margin: 0;
	padding: 0.5rem 0;
	font-size: 1.15rem;
	font-weight: 500;
	color: black;
}
.site-footer #footer-navigation {
	grid-area: siteMap;
	align-self: flex-start;
	padding-top: 0.5rem;
}
.site-footer .acf-map.acf-map-footer {
	grid-area: googleMap;
	align-self: center;
	padding: 0 1rem;
}
.site-footer .site-info {
	grid-area: copyright;
	align-self: center;
	margin: 0 auto;
	padding: 0 1rem;
}
.site-footer .site-info a {
	color: black;
}

#menu-footer-menu {
	list-style: none;
	padding-right: 1rem;
	margin: 0;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	height: 8rem;
	align-content: flex-end;
}
#menu-footer-menu li {
	padding-left: 1.5rem;
	font-size: 1rem;
}

.acf-map.acf-map-footer {
	width: 91%;
	height: 200px;
	border: #ccc solid 1px;
	margin: 0 auto;
}

@media (min-width: 650px) {
	.site-footer .infoWrapperFooter {
		padding-left: 5rem;
	}

	#menu-footer-menu {
		padding-right: 5rem;
	}
}
@media (min-width: 850px) {
	.acf-map.acf-map-footer {
		width: 100%;
		height: 300px;
	}
}
/* Posts and pages
--------------------------------------------- */
.sticky {
	display: block;
}

.page {
	overflow-x: hidden;
}

.post,
.page {
	margin: 0 0 1.5em;
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.acf-map {
	width: 100%;
	max-width: 58rem;
	height: 400px;
	border: #ccc solid 1px;
	margin: 0 auto;
}

.acf-map img {
	max-width: inherit !important;
}

#sb_instagram {
	max-width: 60rem;
	margin: 0 auto;
	padding: 1rem;
}

.page-id-29 .post-thumbnail img {
	object-fit: cover;
	max-height: 45rem;
}

.post-29#post-29 .entry-title {
	text-align: center;
	padding-top: 6rem;
}

.page-id-29 h2 {
	text-align: center;
	font-size: 2rem;
}

.wpforms-container.wpforms-container-full {
	padding: 1rem;
	max-width: 60rem;
	margin: 0 auto;
}

div.wpforms-container-full .wpforms-form .wpforms-submit-container #wpforms-submit-390 {
	width: 100%;
}

#wpforms-390-field_0,
#wpforms-390-field_0-last,
#wpforms-390-field_1,
#wpforms-390-field_2,
#wpforms-submit-390 {
	border-radius: 5px;
}

.infoWrapper {
	display: none;
}

@media (min-width: 600px) {
	.infoWrapper {
		display: flex;
		justify-content: space-between;
		visibility: visible;
		max-width: 60rem;
		padding: 1rem;
		margin: 2rem auto;
		align-items: center;
		font-size: 0.75rem;
	}
	.infoWrapper div {
		max-width: 9rem;
		display: inline-block;
	}
	.infoWrapper div h3 {
		margin: 0;
	}
}
/* Comments
--------------------------------------------- */
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/* Widgets
--------------------------------------------- */
.widget {
	margin: 0 0 1.5em;
}
.widget select {
	max-width: 100%;
}

/* Media
--------------------------------------------- */
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/* Captions
--------------------------------------------- */
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}
.wp-caption img[class*=wp-image-] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/* Galleries
--------------------------------------------- */
.gallery {
	margin-bottom: 1.5em;
	display: grid;
	grid-gap: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	width: 100%;
}

.gallery-columns-2 {
	grid-template-columns: repeat(2, 1fr);
}

.gallery-columns-3 {
	grid-template-columns: repeat(3, 1fr);
}

.gallery-columns-4 {
	grid-template-columns: repeat(4, 1fr);
}

.gallery-columns-5 {
	grid-template-columns: repeat(5, 1fr);
}

.gallery-columns-6 {
	grid-template-columns: repeat(6, 1fr);
}

.gallery-columns-7 {
	grid-template-columns: repeat(7, 1fr);
}

.gallery-columns-8 {
	grid-template-columns: repeat(8, 1fr);
}

.gallery-columns-9 {
	grid-template-columns: repeat(9, 1fr);
}

.gallery-caption {
	display: block;
}

